import { createStore, mapGetters } from "vuex";

export default createStore({
  state: {
    uid : 0,
  },
  getters: {
    getUid: () => {
      return Date.now();
    },
    getUidSeq: state => {
      return state.uid++;
    },
  },
  mutations: {},
  actions: {},
  modules: {},
});
