import { createRouter, createWebHistory, RouteRecordRaw } from "vue-router";
const CreateResponse = () => import("../views/CreateResponse.vue");
const ViewAllResponses = () => import("../views/ViewAllResponses.vue");
const ViewForms = () => import("../views/ViewForms.vue");
const CreateForm = () => import("../views/CreateForm.vue");
const ChooseForm = () => import("../views/ChooseForm.vue");
const Checkin = () => import("../views/CheckIn.vue");
const ValidateSignup = () => import("../views/ValidateSignup.vue");
const Certificate = () => import("../views/Certificate.vue");
const CreateCertificate = () => import("../views/CreateCertificate.vue");
const CreateTicket = () => import("../views/CreateTicket.vue");
const Ticket = () => import("../views/Ticket.vue");
const error404 = () => import("../views/Error404.vue");
const noAccess = () => import("../views/NoAccess.vue");
const badRequest = () => import("../views/BadRequest.vue");
const MyEvents = () => import("../views/MyEvents.vue");
const MyParticipations = () => import("../views/MyParticipations.vue");

const routes: Array<RouteRecordRaw> = [
  {
    path: "",
    name: "Formulários da Plataforma",
    component: ViewForms,
  },
  {
    path: "/respond/:id",
    name: "Resposta a Formulário",
    component: CreateResponse,
  },
  {
    path: "/responses/:id",
    name: "Respostas de Formulário",
    component: ViewAllResponses,
  },
  {
    path: "/createForm/:id?",
    name: "Criador de Formulários",
    component: CreateForm,
  },
  {
    path: "/chooseForm/:id",
    name: "Gerir Formulários e Modelos de Evento",
    component: ChooseForm,
  },
  {
    path: "/checkin/:id",
    name: "Registo de Presença",
    component: Checkin,
  },
  {
    path: "/validate/:id",
    name: "Validar Respostas",
    component: ValidateSignup,
  },
  {
    path: "/certificate",
    name: "Certificado",
    component: Certificate,
  },
  {
    path: "/createCertificate/:id",
    name: "Criador de Certificados",
    component: CreateCertificate,
  },
  
  {
    path: "/ticket",
    name: "Bilhete",
    component: Ticket,
  },
  {
    path: "/createTicket/:id",
    name: "Criador de Bilhetes",
    component: CreateTicket,
  },
  {
    path: "/myEvents",
    name: "Os meus eventos",
    component: MyEvents,
  },
  {
    path: "/myParticipations",
    name: "As minhas inscrições",
    component: MyParticipations,
  },
  {
    path: "/noaccess",
    name: "Sem acesso",
    component: noAccess,
  },
  {
    path: "/badrequest",
    name: "Erro 500",
    component: badRequest,
  },
  {
    path: "/:catchAll(.*)",
    name: "Erro 404",
    component: error404,
  },
];

const router = createRouter({
  history: createWebHistory("/forms/"),
  routes,
});

export default router;
